// В этом файле должны быть стили для БЭМ-блока video, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.video {
	background: $bg-tertiary;
	padding-bottom: 0;
	@media (min-width: $screen-sm) {
		padding-bottom: 48px;
	}
	&__clip {
		box-shadow: 26px 14px 42px rgba(83, 107, 129, 0.14);
		height: 0;
		//height: auto;
		margin: 24px -16px 0;
		overflow: hidden;
		outline: none;
		padding-top: 56.25vw;
		position: relative;
		width: 100vw;
		@media (min-width: $screen-sm) {
			border-radius: 16px;
			margin: 24px 0 0;
			padding-top: 56.25%;
			width: 100%;
		}
		@media (min-width: $screen-md) {
			//height: 387px;
			margin: 56px auto 0;
			//width: 688px;
		}
		@media (min-width: $screen-lg) {
			border-radius: 24px;
			//height: 522px;
			//width: 928px;
		}
		@media (min-width: $screen-xl) {
			//height: 576px;
			//width: 1024px;
		}
	}
	iframe,
	video {
		height: 100%;
		left: 0;
		margin: 0;
		outline: none;
		padding: 0;
		position: absolute;
		top: 0;
		width: 100%;
	}
}
